import type { App } from 'vue';
import CustomForm from './CustomForm/index.vue';

export { default as CustomForm } from './CustomForm/index.vue';

export default {
  install: (app: App) => {
    app.component(CustomForm.name, CustomForm);
    return app;
  },
};

export * from './CustomForm/index.vue';
export * from './CustomForm/types/types';
