<template>
  <div class="neo-form" :style="{ height }">
    <a-row type="flex" class="neo-body">
      <a-col class="left-section" v-if="isDesign">
        <a-tabs v-model:activeKey="selectTab" size="small">
          <a-tab-pane :tab="langs.fieldsTxt" key="tab1">
            <div class="new-elements">
              <div class="panel panel--info">
                <draggable
                  :move="handleMove"
                  itemKey="id"
                  :list="modals"
                  :group="{ name: 'container', pull: 'clone', put: false }"
                  :clone="handleClone"
                >
                  <template #item="{ element }">
                    <div class="button draggable-button">
                      {{ element.title }}
                      <component class="buttonIcon" :is="element.icon" />
                    </div>
                  </template>
                </draggable>
              </div>
            </div>
          </a-tab-pane>
          <a-tab-pane :tab="langs.layoutTxt" key="tab2">
            <div class="new-elements">
              <div class="panel panel--info">
                <draggable
                  :move="handleMove"
                  itemKey="id"
                  :list="containers"
                  :group="{ name: 'container', pull: 'clone', put: false }"
                  :clone="handleClone"
                  @start="handleStart"
                  @end="handleEnd"
                >
                  <template #item="{ element }">
                    <div class="button draggable-button">
                      {{ element.title }}
                    </div>
                  </template>
                </draggable>
              </div>
            </div>
          </a-tab-pane>
        </a-tabs>
      </a-col>
      <a-col class="middle-section">
        <div class="content" v-bar :class="{ shadow: isDesign }">
          <div>
            <div class="form">
              <slot name="preset-fields" class="preset-fields"></slot>
              <ListContainer
                :upload-info="uploadInfo"
                :dragType="dragType"
                :list="currentValue"
                :selected-item="selectedItem"
                :upload-action="uploadAction"
                :domain="domain"
                :mode="mode"
                :header="header"
                :lang="lang"
                :has-colon="hasColon"
                @handleDelete="handleDelete"
              />
            </div>
          </div>
        </div>
      </a-col>
      <a-col class="right-section" v-if="isDesign">
        <a-tabs>
          <a-tab-pane :tab="langs.setting" key="1">
            <div v-if="selectedItem.id">
              <div class="widget-option" v-if="selectedItem.id">
                <div class="sub-title">{{ langs.title }}</div>
                <a-input v-model:value="selectedItem.title" />
              </div>

              <div
                class="widget-option"
                v-if="placeholderWidget.includes(selectedItem.type)"
              >
                <div class="sub-title">{{ langs.defaultVal }}</div>
                <a-input v-model:value="selectedItem.placeholder" />
              </div>

              <div
                class="widget-option"
                v-if="optionsWidget.includes(selectedItem.type)"
              >
                <div class="sub-title">{{ langs.optionsTxt }}</div>
                <a-input
                  v-model:value="item.label"
                  v-for="item in selectedItem.value"
                  :key="item.value"
                >
                  <template #addonBefore>
                    <div
                      class="inputBtn"
                      @click="handleMinusOption(item.value)"
                    >
                      <minus-outlined />
                    </div>
                  </template>
                  <template #addonAfter>
                    <div @click="handleAddOption" class="inputBtn">
                      <plus-outlined />
                    </div>
                  </template>
                </a-input>
              </div>

              <div class="widget-option" v-if="selectedItem.type === 'date'">
                <div class="sub-title">{{ langs.dateFormat }}</div>
                <a-radio-group v-model:value="selectedItem.format" vertical>
                  <a-radio
                    v-for="(item, index) in selectedItem.formatOptions"
                    :key="index"
                    :value="item"
                  >
                    <span>{{ item }}</span>
                  </a-radio>
                </a-radio-group>
              </div>

              <div
                class="widget-option"
                v-if="selectedItem.type === 'inputNumber'"
              >
                <div class="sub-title">{{ langs.numberType }}</div>
                <a-radio-group v-model:value="selectedItem.format" vertical>
                  <a-radio
                    v-for="(item, index) in selectedItem.formatOptions"
                    :key="index"
                    :value="item"
                  >
                    <span>{{ item }}</span>
                  </a-radio>
                </a-radio-group>
              </div>

              <div class="widget-option" v-if="selectedItem.type === 'email'">
                <div class="sub-title">{{ langs.mailSuffix }}</div>
                <a-radio-group v-model:value="selectedItem.hasSuffix" vertical>
                  <a-radio
                    :value="item"
                    v-for="(item, index) in selectedItem.suffixOptions"
                    :key="index"
                  >
                    <span>{{ item }}</span>
                  </a-radio>
                </a-radio-group>
              </div>
            </div>
            <div v-else>
              <a-alert type="warning" show-icon>
                <template #message>
                  <h4>{{ langs.selectComponent }}</h4>
                </template>
              </a-alert>
            </div>
          </a-tab-pane>
        </a-tabs>
      </a-col>
    </a-row>
  </div>
</template>
<script lang="ts">
// import("intersection-observer") as any;
export default {
  name: "NeoCustomFormNext",
};
</script>
<script setup lang="ts">
import draggable from "vuedraggable/src/vuedraggable";
import { ref, nextTick, watch, provide } from "vue";
import getLangs from "./lang";
import getContainers, { getColumns } from "./modal/containers";
import Vuebar from "../vuebar";
import type {
  ContainersProps,
  LangsProps,
  ModalItemProps,
  UploadInfoProps,
} from "./types/types";
import getModals from "./modal/modals";
import ListContainer from "./components/List/ListContainer.vue";
import { MinusOutlined, PlusOutlined } from "@ant-design/icons-vue";

export interface NeoCustomFormProps {
  uploadAction?: string;
  height?: string;
  value?: ModalItemProps[];
  mode?: "design" | "edit" | "view";
  lang?: "zh-CN" | "en-US";
  langPackage?: any;
  header?: () => any;
  hasColon?: boolean;
  domain?: string;
  uploadInfo?: UploadInfoProps;
}
const props = withDefaults(defineProps<NeoCustomFormProps>(), {
  uploadAction: "",
  mode: "design",
  lang: "zh-CN",
  header: () => {},
  hasColon: false,
  domain: "",
  height: "100%",
  value: () => [],
  langPackage: () => ({}),
  uploadInfo: () => ({
    statusKey: "Status",
    successValue: 1,
    uploadFileName: "",
  }), // 文件上传时的fileName 默认 file
});
const emit = defineEmits(["update:value"]);

const vBar = Vuebar.directive;
const selectedItem = ref<ModalItemProps>({} as ModalItemProps),
  optionsWidget = ref(["select", "multiple", "radio", "checkbox"]),
  placeholderWidget = ref(["input", "textarea"]),
  langs = ref<LangsProps>({
    layoutTxt: "",
    fieldsTxt: "",
    dragToCanvas: "",
    option: "",
  }),
  containers = ref<{ id: number; title: string; type: string }[]>([]),
  modals = ref<ModalItemProps[]>([]),
  isDesign = ref(props.mode === "design"),
  selectTab = ref<"tab1" | "tab2">("tab1"),
  dragType = ref("");

const handleClone = (item: ModalItemProps) => {
  // 处理附件组件的fileName
  // 复制时去掉icon 一个icon长度大约400
  let obj: any = {
    icon: "",
  };
  if (item.type === "attachment") {
    obj.fileName = props.uploadInfo.uploadFileName || item.fileName;
  }
  const data = {
    ...item,
    isTem: false,
    id: Date.now(),
    ...obj,
    ...(["container2", "container3"].includes(item.type)
      ? { columns: getColumns(item.id) }
      : {}),
  };

  return data;
};

const currentValue = ref<ModalItemProps[]>([]);
watch(
  () => props.value,
  () => {
    currentValue.value = props.value || [];
  },
  { immediate: true }
);

watch(
  () => currentValue.value,
  () => {
    emit("update:value", currentValue.value);
  },
  { deep: true }
);

const handleMove = (e: any) => {
  if (e.relatedContext?.element) {
    // 禁止单列拖动
    return !e.relatedContext.element.isTem;
  }
};

const selectConponent = (
  data: (ModalItemProps | ContainersProps)[],
  id: number
) => {
  let result: ModalItemProps;
  for (let i = 0; i < data.length; i++) {
    if (data[i].id === id) {
      result = data[i] as ModalItemProps;
      break;
    } else if (Array.isArray(data[i])) {
      result = selectConponent(data[i] as unknown as ModalItemProps[], id);
    } else if ((data[i] as ContainersProps).columns) {
      Object.values((data[i] as ContainersProps).columns).forEach(
        (itemList) => {
          if (!result) {
            result = selectConponent(
              itemList as unknown as ModalItemProps[],
              id
            );
          }
        }
      );
    }
  }
  // @ts-ignore
  return result;
};

//选中组件
const handleSelect = (id: number) => {
  selectedItem.value = selectConponent(currentValue.value, id);
};
provide("handleSelect", handleSelect);
const setDragType = (val: string) => {
  dragType.value = val;
};
provide("setDragType", setDragType);
//递归删除选定组件
const deleteComponent = (
  data: any,
  item: ModalItemProps,
  parent?: any,
  index?: number
) => {
  if (data.id === item.id) {
    parent.splice(index, 1);
  } else if (Array.isArray(data)) {
    data.map((child, index) => deleteComponent(child, item, data, index));
  } else if (data.columns) {
    Object.values(data.columns).map((child, index) =>
      deleteComponent(child, item)
    );
  }
};
//删除组件
const handleDelete = (item: ModalItemProps) => {
  deleteComponent(props.value, item);
  nextTick(() => {
    selectedItem.value = {} as ModalItemProps;
  });
};
//增加option选项
const handleAddOption = () => {
  selectedItem.value?.value?.push({
    value: `${langs.value.option}${selectedItem.value?.value?.length + 1}`,
    label: `${langs.value.option}${selectedItem.value?.value?.length + 1}`,
  });
};
//减去option选项
const handleMinusOption = (value: string) => {
  if (selectedItem.value.value && selectedItem.value.value.length === 1) {
    return;
  }
  selectedItem.value.value = selectedItem.value?.value?.filter(
    (option) => option.value !== value
  );
};

const handleStart = (e: any) => {
  dragType.value = containers.value[e.oldIndex].type;
};
const handleEnd = () => {
  dragType.value = "";
};

const init = () => {
  langs.value = Object.assign({}, getLangs(props.lang), props.langPackage);
  containers.value = getContainers(langs.value);
  modals.value = getModals(langs.value);
};
init();
</script>

<style lang="less">
.neo-form {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 0 6px;
  background-color: #fff;

  .neo-body {
    height: 100%;
    flex-wrap: nowrap;

    .left-section {
      width: 173px;

      .new-elements {
        .button {
          font-size: 13px;
          line-height: 28px;
          color: #666;
          margin: 6px 2%;
          border: 1px dashed #999;
          padding: 5px 5px 5px 15px;
          border-radius: 2px;
          transition: background 0.15s ease-in-out;
          background-color: #f5f5f5;
          position: relative;
          z-index: 100;
          cursor: move;
          text-align: left;

          .buttonIcon {
            float: right;
            font-size: 18px;
            margin: 5px 7px 0 0;
          }
        }
      }
    }

    .middle-section {
      flex: 1 0 0%;
      padding: 0 12px;
      // height: 100%;

      .content {
        position: relative;
        height: 100%;
        background-color: #fff;

        &.shadow {
          border: 1px solid #ccc;
          box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
        }

        .form {
          height: 100%;
          display: flex;
          flex-direction: column;
          padding-top: 12px;
          padding-right: 20px;

          .preset-fields {
            flex-shrink: 0;
          }

          .custom-fields {
            flex: 1 0 auto;
          }

          .vddl-placeholder {
            width: 98%;
            height: 60px;
            border: 1px dashed #ed3f14;
            background: #fff;
            margin: 2px 1%;
            box-sizing: border-box;
          }
        }

        .vb-dragger {
          right: 0;
          border-radius: 20px;
          width: 6px;
          background: rgba(158, 158, 158, 0.64);
          display: none;
        }

        &:hover {
          .vb-dragger {
            display: block;
          }
        }
      }
    }

    .right-section {
      width: 173px;

      .sub-title {
        text-align: left;
      }
      .ant-input-group-addon {
        padding: 0;
      }
      .inputBtn {
        padding: 0 11px;
        cursor: pointer;
      }

      .ant-input-group-wrapper {
        margin: 8px 0;
      }

      .ant-alert-message {
        h4 {
          font-size: 12px;
          font-weight: normal;
        }
      }
    }
  }
  .listContainer {
    flex: 1 0 auto;
    min-height: 45px;

    .ghost {
      height: 55px;
      position: relative;
      &:before {
        position: absolute;
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        border: 1px dashed #ed3f14;
        z-index: 1;
        background: #fff;
      }
    }
    &.form-row-item {
      padding: 2px 0;
      position: relative;

      .componentBox {
        &.form-row-item-active {
          background-color: #f9f9f9;
          padding-right: 1px;

          .remove-btn {
            display: block;
          }
        }
        position: relative;
        padding: 5px 0;
      }
      .component {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        width: 100%;
        padding: 0 50px 0 40px;
        margin: 5px 0;
        min-height: 35px;
        position: relative;

        .component-title {
          width: 100px;
          text-align: right;
          padding-right: 12px;
        }

        .component-content {
          flex: 1;

          .nodrag {
            text-align: left;
          }
          .ant-select {
            width: 100%;
          }

          .ivu-upload {
            position: relative;
            display: flex;
            align-items: center;

            &.view-mode {
              .ivu-upload-select,
              .ivu-upload-list-remove {
                display: none;
              }

              .ivu-upload-list {
                margin-top: 0;
              }
            }

            .ivu-upload-list {
              float: left;

              .ivu-upload-list-file {
                float: left;
              }
            }
          }
        }

        &.container-row-item {
          border-left: none;
          border-right: none;

          &.form-row-item-active {
            border: 1px solid #dcdee2;

            .container-column {
              border-right: 1px solid #dcdee2;
            }
          }

          .container-column {
            flex: 1;
            min-height: 45px;

            .component {
              padding-right: 30px;
            }

            &:last-child {
              border-right: none;
            }
          }
        }
      }

      .remove-btn {
        display: none;
        position: absolute;
        right: 5px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 20px;
        color: #ed3f14;
        cursor: pointer;
      }

      .container-column {
        .component {
          padding: 0;
        }
      }
    }
  }
}
</style>
