<template>
  <draggable
    itemKey="id"
    ghostClass="ghost"
    :list="props.list"
    group="container"
    class="listContainer form-row-item"
    @choose="handleListSelect"
    @add="handleAdd"
    @start="handleStart"
    @end="handleEnd"
    :disabled="
      ((dragType === 'container' ||
        dragType === 'container2' ||
        dragType === 'container3') &&
        containerIndex !== undefined) ||
      isView ||
      isEdit
    "
  >
    <!-- 禁用条件 1. 拖拽的是容器组件 并且 目标也是容器 2. 预览模式 -->
    <!-- handle=".component" -->
    <template #item="{ element: item, index: itemIndex }">
      <div
        class="componentBox"
        :class="[
          selectedItem.id === item.id && isDesign ? 'form-row-item-active' : '',
        ]"
      >
        <!-- 布局控件 -->
        <div
          class="component container-row-item"
          v-if="
            item.type === 'container' ||
            item.type === 'container2' ||
            item.type === 'container3'
          "
          :class="{
            'form-row-item-active': item.hasOwnProperty('columns') && isDesign,
          }"
        >
          <div
            class="container-column"
            v-for="(column, index) in item.columns"
            :key="index"
          >
            <ListContainer
              :upload-info="uploadInfo"
              :dragType="dragType"
              :container-index="itemIndex"
              :list="column"
              :domain="domain"
              :mode="mode"
              :selected-item="selectedItem"
              @handleDelete="handleDelete"
              :header="header"
              :lang="lang"
              :hasColon="hasColon"
              :uploadAction="uploadAction"
            >
            </ListContainer>
          </div>
        </div>
        <!-- 文本框组件 -->
        <div class="component" v-if="item.type === 'input'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">{{ item.model }}</div>
            <div class="nodrag" v-else>
              <a-input
                v-model:value="item.model"
                :placeholder="item.placeholder"
              />
            </div>
          </div>
        </div>
        <!-- 多行文本框组件 -->
        <div class="component" v-else-if="item.type === 'textarea'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">{{ item.model }}</div>
            <div class="nodrag" v-else>
              <a-textarea
                v-model:value="item.model"
                :placeholder="item.placeholder"
              />
            </div>
          </div>
        </div>

        <!-- 单选下拉框组件 -->
        <div class="component" v-else-if="item.type === 'select'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">
              {{ getSelectLabel(item) }}
            </div>
            <div class="nodrag" v-else>
              <a-select
                v-model:value="item.model"
                :placeholder="item.placeholder"
                transfer
              >
                <a-select-option
                  v-for="m in item.value"
                  :value="m.value"
                  :key="m.label"
                  >{{ m.label }}</a-select-option
                >
              </a-select>
            </div>
          </div>
        </div>

        <!-- 多选下拉框组件 -->
        <div class="component" v-else-if="item.type === 'multiple'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">
              {{ getMultipleLabels(item) }}
            </div>
            <div class="nodrag" v-else>
              <a-select
                v-model:value="item.model"
                mode="multiple"
                :placeholder="item.placeholder"
                transfer
              >
                <a-select-option
                  v-for="m in item.value"
                  :value="m.value"
                  :key="m.label"
                  >{{ m.label }}</a-select-option
                >
              </a-select>
            </div>
          </div>
        </div>

        <!-- 单选框组件 -->
        <div class="component" v-else-if="item.type === 'radio'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">{{ item.model }}</div>
            <div class="nodrag" v-else>
              <a-radio-group
                v-model:value="item.model"
                class="ivu-middle-group"
              >
                <a-radio
                  v-for="m in item.value"
                  :value="m.label"
                  :key="m.value"
                  :disabled="item.isCheck"
                  >{{ m.label }}</a-radio
                >
              </a-radio-group>
            </div>
          </div>
        </div>

        <!-- 复选框组件 -->
        <div class="component" v-else-if="item.type === 'checkbox'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">
              {{ item.model.join(",") }}
            </div>
            <div class="nodrag" v-else>
              <a-checkbox-group
                v-model:value="item.model"
                class="ivu-middle-group"
              >
                <a-checkbox
                  v-for="m in item.value"
                  :key="m.value"
                  :value="m.label"
                  >{{ m.label }}</a-checkbox
                >
              </a-checkbox-group>
            </div>
          </div>
        </div>

        <!-- 日期组件 -->
        <div class="component" v-else-if="item.type === 'date'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">{{ item.model }}</div>
            <div class="nodrag" v-else>
              <a-date-picker
                v-model:value="item.model"
                type="date"
                :format="item.format"
                :value-format="item.format"
                :placeholder="item.placeholder"
                transfer
              ></a-date-picker>
            </div>
          </div>
        </div>

        <!-- 数字输入框组件 -->
        <div class="component" v-else-if="item.type === 'inputNumber'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">{{ item.model }}</div>
            <div class="nodrag" v-else>
              <a-input-number
                :min="0"
                v-model:value="item.model"
              ></a-input-number>
            </div>
          </div>
        </div>

        <!-- 邮箱组件 -->
        <div class="component" v-else-if="item.type === 'email'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="view-model" v-if="isView">
              {{ item.model }}{{ item.append }}
            </div>
            <div class="nodrag" v-else>
              <a-input
                v-model:value="item.model"
                :placeholder="item.placeholder"
              >
                <template #addonAfter>
                  <a-select
                    v-model:value="item.append"
                    style="width: 100px"
                    v-show="['显示', 'show'].includes(item.hasSuffix)"
                  >
                    <a-select-option value=".com">.com</a-select-option>
                    <a-select-option value=".cn">.cn</a-select-option>
                  </a-select>
                </template>
              </a-input>
            </div>
          </div>
        </div>

        <!-- 超链接组件 -->
        <div class="component" v-else-if="item.type === 'link'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <a
              target="_blank"
              v-if="isView"
              style="display: block"
              class="view-model"
              :href="(item.prepend || '') + item.model + (item.append || '')"
              >{{ item.prepend || "" }}{{ item.model
              }}{{ item.append || "" }}</a
            >
            <div class="nodrag" v-else>
              <a-input
                v-model:value="item.model"
                :placeholder="item.placeholder"
              >
                <template #addonBefore>
                  <a-select v-model:value="item.prepend" style="width: 100px">
                    <a-select-option value="http://">http://</a-select-option>
                    <a-select-option value="https://">https://</a-select-option>
                  </a-select>
                </template>
                <template #addonAfter>
                  <a-select v-model:value="item.append" style="width: 100px">
                    <a-select-option value=".com">.com</a-select-option>
                    <a-select-option value=".cn">.cn</a-select-option>
                  </a-select>
                </template>
              </a-input>
            </div>
          </div>
        </div>

        <!-- 附件组件 -->
        <div class="component" v-else-if="item.type === 'attachment'">
          <div class="component-title">
            {{ item.title }}
            <span v-if="hasColon">：</span>
          </div>
          <div class="component-content">
            <div class="nodrag attachment">
              <a-upload
                :ref="
                  (el:any) => {
                    setUploadRef(el, item.id);
                  }
                "
                @preview="onPreview"
                @remove="
                  (file: any) => {
                    onRemove(file, item);
                  }
                "
                :default-file-list="item.defaultList"
                :headers="uploadHeaders"
                :before-upload="beforeUpload"
                @change="handleFileChange($event, item)"
                :action="isView ? '' : uploadAction || item.action"
                :name="item.fileName"
                :class="{ 'view-mode': isView }"
              >
                <a-button>
                  <upload-outlined></upload-outlined>{{ langs.upload }}
                </a-button>
              </a-upload>
            </div>
          </div>
        </div>

        <!-- 删除组件 -->
        <a-popconfirm
          v-if="item.type !== 'container' && isDesign"
          class="remove-btn"
          :title="langs.removeConfirm"
          width="184"
          placement="topRight"
          @confirm="handleDelete(item)"
          transfer
        >
          <close-circle-outlined :title="langs.remove" />
        </a-popconfirm>
      </div>
    </template>
  </draggable>
</template>

<script setup lang="ts">
import draggable from "vuedraggable/src/vuedraggable";
import { inject, ref } from "vue";
import getLangs from "../../lang";
import getModals from "../../modal/modals";
import { CloseCircleOutlined, UploadOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import type {
  LangsProps,
  ModalItemProps,
  UploadInfoProps,
} from "../../types/types";
const props = withDefaults(
  defineProps<{
    uploadAction: string;
    list: ModalItemProps[];
    selectedItem: ModalItemProps;
    mode: "design" | "edit" | "view";
    lang: "zh-CN" | "en-US";
    header: () => any;
    hasColon: boolean;
    domain: string;
    containerIndex: number | undefined;
    dragType: string;
    uploadInfo: UploadInfoProps;
  }>(),
  {
    uploadAction: "",
    item: () => {},
    list: () => [],
    selectedItem: () => ({} as ModalItemProps),
    mode: "design",
    lang: "zh-CN",
    header: () => {},
    hasColon: false,
    domain: "",
    containerIndex: undefined,
    dragType: "",
  }
);

const emit = defineEmits(["handleDelete"]);
const uploadHeaders = ref({}),
  langs = ref<LangsProps>({}),
  attachmentModal = ref<ModalItemProps>({} as ModalItemProps),
  isDesign = ref(props.mode === "design"),
  isEdit = ref(props.mode === "edit"),
  isView = ref(props.mode === "view"),
  uploadRefList = ref<any>({});

//删除组件
const handleDelete = (item: ModalItemProps) => {
  emit("handleDelete", item);
};
const handleSelect: any = inject("handleSelect");
//选择组件
const handleListSelect = (e: any) => {
  handleSelect(props.list[e.oldIndex].id);
};

const handleAdd = (e: any) => {
  handleSelect(props.list[e.newIndex].id);
};

const openFile = (url: string) => {
  if (url) {
    if (props.domain) {
      url = props.domain + url;
      window.open(url.replace(/#/g, "%23"), "_blank");
    } else {
      window.open(url.replace(/#/g, "%23"), "_blank");
    }
  } else {
    console.log("url不存在");
  }
};

const setUploadRef = (el: any, id: number) => {
  uploadRefList.value[id + ""] = el;
};

//文件上传成功
const onSuccess = (
  response: any,
  file: any,
  fileList: any,
  item: ModalItemProps
) => {
  if (response[props.uploadInfo.statusKey] === props.uploadInfo.successValue) {
    if (item.nameMapping) {
      let name: any = {};
      Object.assign(name, response);
      item.nameMapping.split(".").map((key) => {
        name = name[key];
      });
      file.name = (name as string).split("/").pop();
    }

    if (item.urlMapping) {
      let url: any = {};
      Object.assign(url, response);
      item.urlMapping.split(".").map((key) => {
        url = url[key];
      });
      file.url = url;
    }

    item.defaultList = fileList;
  } else {
    message.warning(response.message || response.Message);

    // const fileList = uploadRef.fileList;
    fileList.splice(fileList.indexOf(file), 1);
  }
};
const setDragType: any = inject("setDragType");
const handleStart = (e: any) => {
  setDragType(props.list[e.oldIndex].type);
};
const handleEnd = () => {
  setDragType("");
};
const handleFileChange = ({ file, fileList }: any, item: ModalItemProps) => {
  if (file.error) {
    onError(file.error, fileList, file);
  } else if (file.response) {
    onSuccess(file.response, file, fileList, item);
  }
};
//文件上传失败
const onError = (error: any, fileList: any, file: any) => {
  message.error((error && error.message) || langs.value.uploaderFail);
  fileList.splice(fileList.indexOf(file), 1);
};
//上传文件之前
const beforeUpload = () => {
  Object.assign(uploadHeaders.value, props.header());
};
//删除文件
const onRemove = (file: any, item: ModalItemProps) => {
  item.defaultList = uploadRefList.value[item.id + ""].fileList;
};

//点击已上传的文件
const onPreview = (file: any) => {
  console.log(file);

  openFile(file.response.Data.FileRelativePath);
};

//获取multiple已选项对应的label
const getMultipleLabels = (item: ModalItemProps) => {
  if (item.model && item.value) {
    const labels = item.value
      .filter((v) => {
        return item.model && (item.model as string[]).includes(v.value);
      })
      .map((v) => {
        return v.label;
      });

    return labels.join(",");
  } else {
    return [];
  }
};

//获取单选下拉框已选项对应的label
const getSelectLabel = (item: ModalItemProps) => {
  if (item.model && item.value) {
    const label = item.value.map((v) => {
      if (v.value === item.model) {
        return v.label;
      }
    });
    return label.join("");
  }
  return "";
};

//兼容附件老数据
const formatOldFileData = (data: ModalItemProps) => {
  if (data.model && typeof data.model === "string") {
    const name = data.model.split("/").pop();
    const { defaultList, ...attachmentModalFields } = attachmentModal.value;
    Object.assign(defaultList as object, {
      name,
      response: {
        ResponseBody: data.model,
      },
      url: data.model,
    });

    Object.assign(data, {
      ...attachmentModalFields,
      defaultList,
    });
  }
};
const initPropsFile = (
  list: ModalItemProps[],
  uploadFileName?: string
): ModalItemProps[] => {
  list.forEach((item) => {
    if (item?.type === "attachment") {
      Object.assign(item, {
        fileName: uploadFileName || item.fileName,
      });
      formatOldFileData(item);
      if (item.defaultList) {
        item.defaultList.map((file) => {
          file.showProgress = false;
        });
      } else {
        Object.assign(item, {
          defaultList: [],
        });
      }
    }
  });
  return list;
};
const init = () => {
  langs.value = getLangs(props.lang);
  attachmentModal.value =
    getModals(langs.value).pop() || ({} as ModalItemProps);
  initPropsFile(props.list, props.uploadInfo.uploadFileName);
};

init();

defineExpose({
  initPropsFile,
});
</script>

<style scoped></style>
