import type { LangsProps } from "../types/types";

export const getColumns = (id: number) => {
  if (id === 1) {
    return {
      l: [],
      r: [],
    };
  } else if (id === 5000) {
    return {
      l: [],
      m: [],
      r: [],
    };
  }
  return {};
};

/**
 * 自定义表单组件类
 */
const getContainers = (langs: LangsProps) => {
  return [
    {
      isTem: true,
      id: 1,
      type: "container2",
      columns: getColumns(1),
      title: langs.container12,
    },
    {
      isTem: true,
      id: 5000,
      type: "container3",
      columns: getColumns(5000),
      title: langs.container13,
    },
  ];
};
export default getContainers;
